@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&family=Karla:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400&family=Poppins:ital,wght@0,400;0,500;0,600;0,800;1,300&display=swap');
body{
  background-color: #0B2434;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-section{
  /* position: relative; */
  padding: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #F5F5F5;
  border-radius:10px;
  padding: 10px;

}
.fake-container{
  width: 100%;
  height: 100%;
  position: relative;
}
.main-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu-container{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: end;
  align-items: center;

}

.menu{
 border: 1px solid black;
 border-radius: 5px;
 background-color: rgba(255, 255, 255, 0.915);
 padding: 5px;
}

.menu-text{
  margin: 0;
}

.main-menu{
 position: absolute;
 width: 100%;
 height: 100%;
 background-color: rgba(255, 255, 255, 0.903);
 display: flex;
 flex-direction: column;
}

.close-menu-button{
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: end;
  align-items: center;
  text-align: center;
}
.close-icon{
  margin-right: 20px;
}
.menu-list-container{
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.menus{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  border: 1px solid black;
  background-color: rgba(255, 255, 255, 0.915);
  max-height: 100px;
  gap: 20px;
}
.tenzies--title{
height: 31.35999870300293px;
width: 197.1199951171875px;
left: 0px;
top: 0px;
border-radius: nullpx;
font-family: Karla;
font-size: 26px;
font-weight: 700;
line-height: 30px;
letter-spacing: -0.03em;
text-align: center;
}

.tenzies--instructions{
  font-family: Inter;
  color: #4A4E74;
  width: 232px;
  height: max-content;
  padding-right: 40px; 
  padding-left: 40px;
  margin-left: 10px; 
}

.tenzies-main-game{
  list-style: none;
  width: 300px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  flex-wrap: wrap;
  gap: 30px;
}
.tenzie--tile{
  height: 35.84000015258789px;
width: 35.84000015258789px;
border-radius: 3.8127658367156982px;

  background-color: #FFFFFF;
box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
border-radius: 3.81277px;
text-align: center;
line-height: 30px;
font-family: 'Karla';
font-weight: 800;
font-size: larger;
}

.tenzies--roll-button{
  background-color:#5035FF;
  box-shadow: 0px 3.1999998092651367px 7.679999828338623px 0px #0000002E;
  border-radius: 3.81277px;
  height: 35.84000015258789px;
  width: 92.15999603271484px;
  color: #FFFFFF;
  font-family: 'Karla';
font-style: normal;
font-weight: 700;
font-size: 16.384px;
/* line-height: 19px; */
margin-top: 50px;
border: #5035FF;
padding: 5%;
padding-top: 2.5%;
width: max-content;
}

.green{
  background-color: #59E391;
}

